.main-block {
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    // padding-top: 28px
    // margin: 0 0 0 -10px
    @include lg {
        padding-left: 60px; }
    @include md {
        min-height: auto;
        flex-direction: column;
        padding-left: 60px;
        padding-top: 25px;
        padding-bottom: 20px; }
    @include ms {
        max-width: 300px; }
    &__img_md img {
        display: block;
        max-width: 100%; }
    &__img_md {
        display: none;
        @include md {
            display: block; } }
    &__left {
        max-width: 506px;
        margin-right: 50px;
        min-width: 360px;
        @include md {
            margin: 0;
            min-width: auto;
            max-width: 100%; }
        @include xs {
            min-width: auto; } }
    &__right {
        min-width: 325px;
        width: 100%;
        padding-left: 150px;
        @include lg {
            padding: 0; } }
    &__title {
        max-width: 270px;
        padding-bottom: 40px;
        @include md {
            max-width: 100%; } }
    &__img {
        background-image: url(../img/item1.png);
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: 75% 50%; }
    &__desc {
        border-top: 1px solid #394550;
        color: #394550;
        font-size: 18px;
        font-weight: 400;
        line-height: 28px;
        letter-spacing: -0.09px;
        padding-top: 40px; }
    &__desc p {
        padding: 0 0 40px 0; }
    &__btn {
        display: flex;
        align-items: center;
        @include sm {
            margin-bottom: 20px; } }
    &__btn img {
        cursor: pointer; }
    &__btn span {
        display: none;
        color: #0067df;
        font-size: 16px;
        text-transform: uppercase;
        padding-left: 25px;
        font-weight: 700;
        cursor: pointer;
        @include lg {
            display: block; }
        @include sm {
            padding-left: 15px;
            font-size: 14px; } }
    &__btn_download:hover ~ span {
        display: block; } }
