.out {
    font-family: "Gilroy";
    overflow: hidden; }

.wrapper {
    padding: 0 30px;
    max-width: 1800px;
    margin: 0 auto;
    @include md {
        padding: 0 20px; }
    @include sm {
        padding: 0 10px; } }

.inner {
    padding-top: 139px;
    min-height: 100vh;
    @include sm {
        padding-top: 0px; } }

.bg_black {
    background-color: #283038; }

.bg_white {
    background-color: #ffffff; }

.btn_download {
    background-image: url(../img/download.png);
    background-repeat: no-repeat;
    display: block;
    width: 50px;
    height: 50px;
    background-position: -5px -5px;
    cursor: pointer;
    &:hover {
        width: 50px;
        height: 50px;
        background-position: -65px -5px; } }

