.button_white-lg {
    min-width: 280px;
    padding: 30px 0;
    background-color: #ececec;
    color: #0067df;
    font-family: "Gilroy";
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: -0.01px;
    transition: 0.3s;
    &:hover {
        background-color: #0067df;
        color: #ffffff; }
    @include xxl {
        min-width: 250px; }
    @include xl {
        min-width: 210px;
        padding: 25px 0; }
    @include sm {
        padding: 20px 0;
        font-size: 14px; } }

.button_border-white-lg {
    min-width: 280px;
