.investment {
    display: flex;
    min-height: 100vh;
    background-color: #283038;
    @include sm {
        flex-direction: column-reverse;
        justify-content: space-between; }
    &__left {
        min-width: 325px;
        width: 30%;
        @include sm {
            height: 335px;
            min-width: 250px; } }
    &__img {
        background-image: url(../img/item3.png);
        position: relative;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: bottom left;
        @include lg {
            background-position: 80% 0%; }
        &:after {
            content: "";
            border-right: 1px solid #ececec;
            position: absolute;
            bottom: 0;
            height: 95%;
            width: 100%;
            @include sm {
                border-right: none;
                border-top: 1px solid #ececec;
                right: 50%;
                transform: translateX(50%);
                width: 95%;
                height: 100%; } } }
    &__right {
        padding-top: 210px;
        padding-left: 150px;
        max-width: 690px;
        position: relative;
        @include lg {
            padding-left: 50px;
            padding-right: 50px; }
        @include sm {
            padding: 50px 29px 0 29px; } }
    &__title {
        color: #ececec;
        padding-bottom: 50px; }
    &__desc {
        color: #ececec; }
    &__desc p {
        padding-bottom: 30px; }
    &__btn_download:hover ~ span {
        display: block; } }
