.footer__right {
  background-color: #004596;
  padding: 0;
}

@media only screen and (max-width: 1023px) {
  .footer__right {
    width: 100%;
    height: 500px;
  }
}

.footer__title {
  color: #ffffff;
  margin-bottom: 50px;
}

.footer__contact {
  border-top: 2px solid #649fe4;
  padding-top: 50px;
}

.footer__map {
  background-image: url(../img/map.png);
  width: 100%;
  background-position: 53%;
  background-size: cover;
}

@media only screen and (max-width: 1023px) {
  .footer__map {
    height: 150%;
    background-repeat: no-repeat;
    background-position: 53% -100px;
    background-size: cover;
  }
}

.address {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
}

.address__item {
  margin-bottom: 30px;
}

.address__title {
  padding-left: 32px;
  position: relative;
  margin-bottom: 20px;
}

.address__title:before {
  content: url(../img/address.png);
  position: absolute;
  left: 0;
}

.contact__item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1023px) {
  .contact__item {
    flex-wrap: wrap;
  }
}

.contact__title {
  min-width: 162px;
  margin-right: 45px;
}

@media only screen and (max-width: 1023px) {
  .contact__title {
    padding-bottom: 15px;
  }
}

.contact__img {
  margin-right: 20px;
}

.contact__img img {
  display: block;
  cursor: pointer;
}
